import React from 'react'
import './main.css'
import 'bootstrap/css/bootstrap.css'
import Button from '@material-ui/core/Button'
import DrawerBar from './Drawer'
import {createMuiTheme,ThemeProvider} from '@material-ui/core/styles'
class ActionBar extends React.Component{
    theme = createMuiTheme({
        overrides:{
            MuiButton:{
                root:{
                    '&:hover': {
                        ouline:'none'
                        },
                    '&:active': {
                        outline:'none'
                        },
                    '&:focus': {
                        outline:'none',
                        },
            }
    }
    }
    })
    state = {
        open:false
    }
    handleState=event=>{
        let open = event
        this.setState({open})
    }
    
    render(){
        return (
        <div className="navbar navbar-primary bg-light">
                <a href="#root" className="navbar-brand text-theme"><b>kahani.com</b></a>
                <ThemeProvider theme={this.theme}>
                <Button onClick={()=>{this.handleState(true)}} variant="outlined" color="primary"><b>login</b></Button>
                </ThemeProvider>
                <DrawerBar data={this.state.open} onHandleState={this.handleState}/>
        </div>        
        )
    }
}
export default ActionBar
