import React from 'react'
import TextNode from './TextNode'
import MediaCard from './Cards'
import Container from '@material-ui/core/Container'
import './main.css' 
let comic = require("./dataBase/Comics.json")
class Comics extends React.Component{
    render(){
        return (
        <React.Fragment>
                <TextNode Heading='Comics'>Lorem ipsum dolor sit amet, consectetur adipisicing elit. Quos blanditiis tenetur unde suscipit, quam beatae rerum inventore consectetur, neque doloribus, cupiditate numquam dignissimos laborum fugiat deleniti? Eum quasi quidem quibusdam..</TextNode>
            <Container variant="fluid" align="center" style={{marginBottom:120+'px'}} >
                           {comic.map(item =>
                           <MediaCard title={item.name}>
                           {item.contant}
                           </MediaCard>)}
            </Container>
        </React.Fragment>
        )
    }
}

export default Comics