import React from 'react'
import NavBar from './Components/NavBar'
import ActionBar from './Components/ActionBar'
import CustomizedTabs from './Components/Tabs'
import Comics from './Components/Comics'
import Mythological from './Components/Mythological'
import ScienceFiction from './Components/ScienceFiction'
class App extends React.Component{
    state = {
        type:0,
        value:[<Comics/>,<Mythological/>,<ScienceFiction/>]
    } 
    handleChange=event=>{
    let type = event
    this.setState({type,value:[...this.state.value]})
    }
    render(){
        return (
            <React.Fragment>
            <ActionBar  />
            <NavBar  />
            <CustomizedTabs onHandleChange={this.handleChange} />
            {this.state.value[this.state.type]}
            </React.Fragment>
            )}   
}
export default App