import React from 'react';
import BottomNavigation from '@material-ui/core/BottomNavigation';
import BottomNavigationAction from '@material-ui/core/BottomNavigationAction';
import WhatshotIcon from '@material-ui/icons/Whatshot';
import RestoreIcon from '@material-ui/icons/Restore';
import InfoIcon from '@material-ui/icons/Info';
import HomeIcon from '@material-ui/icons/Home';
import { createMuiTheme, ThemeProvider } from '@material-ui/core/styles';
import './main.css'
export default function NavBar() {
  const theme = createMuiTheme({
      overrides:{
          MuiBottomNavigation:{
           root:{
              backgroundColor:'#147b2c',
           }
       },
         MuiBottomNavigationAction:{
            root:{
                color:'white',
                 '&$selected': {
                    color: 'white',
                    outline:'none'
           },
                
        }
         }, 
      },
  })
  const [value, setValue] = React.useState('Home');
  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  return (
      <ThemeProvider theme={theme} >
    <BottomNavigation value={value} onChange={handleChange} className="bottom-nav">
      <BottomNavigationAction label="Home" value="Home" icon={<HomeIcon />} />
      <BottomNavigationAction label="Recents" value="Recents" icon={<RestoreIcon />} />
      <BottomNavigationAction label="Trending" value="Trending" icon={<WhatshotIcon />} />
       <BottomNavigationAction label="About us" value="about us" icon={<InfoIcon />} />
    </BottomNavigation>
      </ThemeProvider>);
}