import React from 'react'
import Typography from '@material-ui/core/Typography'
import './main.css'
import {createMuiTheme,ThemeProvider} from '@material-ui/core/styles'
class TextNode extends React.Component{
    theme = createMuiTheme({
        overrides:{
            MuiTypography:{
                root:{
                    color:'#147b2ccc',
                }
            }
        }
    })
    render(){
        return(
        <div><ThemeProvider theme={this.theme}>
           <Typography variant="h3" component="h3" align="center" style={{margin:15+'px',fontFamily:"'Comfortaa', cursive"}}>{this.props.Heading}</Typography>
            <Typography align="center" variant="body2" component="p" style={{marginTop:40+'px',fontSize:25+'px',fontFamily:"'Comfortaa', cursive"}}> {this.props.children}</Typography>
                </ThemeProvider>
        </div>
        )
    }
}
export default TextNode