import React from 'react' 
import Drawer from '@material-ui/core/Drawer';
import VpnKeyIcon from '@material-ui/icons/VpnKey'
import Typography from '@material-ui/core/Typography'
import TextFields from './TextField'
import Button from '@material-ui/core/Button'
class DrawerBar extends React.Component{
    render(){
        return(
        <React.Fragment>
          <Drawer anchor={"bottom"} open={this.props.data} onClose={()=>{this.props.onHandleState(false)}}>
            <Typography variant="h4" component="h4" color="primary" align="center" style={{marginTop:20+'px',fontFamily:'Comfortaa',}}><VpnKeyIcon fontSize="large"/> login </Typography>
            <TextFields key='1' label="username" />
            <TextFields key='2' label="password" type="password" /> 
            <Button variant="contained" color="primary" style={{padding:15+'px',marginTop:10+'px'}} ><b>login</b></Button> 
          </Drawer>
        </React.Fragment>
        )
    }
}
export default DrawerBar